import React from "react";
import "./style.css";

class App extends React.Component {
  state = { choose: 0 };

  resultB = e => {
    e.preventDefault();
    let year = "Zysk roczny: ";
    let mnth = "Zysk miesięczny: ";

    if (
      document.getElementById("sprice").value &&
      document.getElementById("percent").value
    ) {
      year +=
        ((document.getElementById("sprice").value *
          document.getElementById("percent").value) /
          100) *
        0.81;
      mnth +=
        ((((document.getElementById("sprice").value *
          document.getElementById("percent").value) /
          100) *
          0.81)) /
        12;
    } else {
      year = "";
      mnth = "";
    }
    document.getElementById(
      "result_bank"
    ).innerHTML = `<div>${year}</div><div>${mnth}</div>`;
  };
  resultG = e => {
    e.preventDefault();
    let kmsltrs = "Spalanie: ";

    if (
      document.getElementById("litres").value &&
      document.getElementById("km").value
    ) {
      kmsltrs +=
        (document.getElementById("litres").value /
          document.getElementById("km").value) *
        100;
      kmsltrs += "l/100km";
    } else {
      kmsltrs = "";
    }

    let cost = "Koszt: ";

    if (document.getElementById("gprice").value) {
      cost +=
        document.getElementById("litres").value *
        document.getElementById("gprice").value;
    } else {
      cost = "";
    }

    document.getElementById(
      "result_gasoline"
    ).innerHTML = `<div>${kmsltrs}</div><div>${cost}</div>`;
  };

  renderCalculator() {
    if (this.state.choose === 0) {
      return (
        <form>
          <b>Kalkulator spalania: </b>
          <label>Ilość zatankowanych litrów: </label>
          <input id="litres"></input>
          <label>Ilość przejechanych kilometrów: </label>
          <input id="km"></input>
          <label>Cena paliwa za litr: </label>
          <input id="gprice"></input>
          <br />
          <button onClick={this.resultG}>Policz:</button>

          <div id="result_gasoline"></div>
        </form>
      );
    } else {
      return (
        <form>
          <b>Kalkulator zysku z RO: </b>
          <label>Kwota startowa: </label>
          <input id="sprice"></input>
          <label>Oprocentowanie(wpisz liczbę bez %): </label>
          <input id="percent"></input>
          <br />
          <button onClick={this.resultB}>Policz:</button>

          <div id="result_bank"></div>
        </form>
      );
    }
  }
  onClick = () => {
    if (
      document.getElementById("menu").style.width === "400px" ||
      document.getElementById("menu").style.width === "100px"
    ) {
      document.getElementById("menu").style.width = "20px";
      document.getElementById("menu").style.transitionDuration = "0.5s";
      document.getElementById("calculator_1").style.display = "none";
      document.getElementById("calculator_1").style.transitionDuration = "0.5s";
      document.getElementById("calculator_2").style.display = "none";
      document.getElementById("calculator_2").style.transitionDuration = "0.5s";
      document.getElementById("instruction").style.display = "flex";
      document.getElementById("prev").style.display = "none";
    } else {
      if (window.innerWidth < 600) {
        document.getElementById("menu").style.width = "100px";
      } else {
        document.getElementById("menu").style.width = "400px";
      }
      document.getElementById("menu").style.transitionDuration = "0.5s";
      document.getElementById("calculator_1").style.display = "flex";
      document.getElementById("calculator_1").style.transitionDuration = "0.5s";
      document.getElementById("calculator_2").style.display = "flex";
      document.getElementById("prev").style.display = "flex";
      document.getElementById("calculator_2").style.transitionDuration = "0.5s";
      document.getElementById("instruction").style.display = "none";
    }
  };

  choose_0 = () => {
    this.setState({ choose: 0 });
  };

  choose_1 = () => {
    this.setState({ choose: 1 });
  };

  render() {
    return (
      <div className="App">
        <div className="navbar">Kalkulatory</div>
        <div className="main">
          <div className="calc">{this.renderCalculator()}</div>
          <div className="menu" id="menu">
            <div id="instruction" onClick={this.onClick}>
              <div>R</div>
              <div>O</div>
              <div>Z</div>
              <div>W</div>
              <div>I</div>
              <div>Ń</div>
            </div>
            <div
              id="calculator_1"
              onClick={this.choose_0}
              className="calculator"
            >
              Kalkulator spalania
            </div>
            <div
              id="calculator_2"
              onClick={this.choose_1}
              className="calculator"
            >
              Kalkulator zysku z rachunku oszczędnościowego
            </div>

            <div id="prev" onClick={this.onClick}>
              W S T E C Z
            </div>
          </div>
        </div>

        <footer>Mikołaj Sojka</footer>
      </div>
    );
  }
}

export default App;
